import React, { Component } from "react";
import Header from "./components/Header";
import Main from "./components/Main";

import axios from "axios";

const config = {
  app_id: "4778506a",
  app_key: "3ae1f03ccf99f7f60b3bbc46c622136e",
  base_url: "https://api.adzuna.com/v1/api/jobs",
  base_params: "search/1?results_per_page=20",
};

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      headerClass: "header",
      jobBg: "",
      jobDetailTitle: "",
      detailPage: false,
      jobs: [],
    };
  }

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll, true);
    // this.handleGetRequest();
    // use this api instead https://jooble.org/api/about
  }

  decodeParams = (searchParams) =>
    Array.from(searchParams.keys()).reduce(
      (acc, key) => ({ ...acc, [key]: searchParams.get(key) }),
      {}
    );

  handleGetRequest = async (params) => {
    const decodedParams = this.decodeParams(
      new URLSearchParams("search=php&location=casablanca")
    );
    const { search, location, country = "gb" } = decodedParams;

    console.log(decodedParams);

    const targetURL = `https://cors-anywhere.herokuapp.com/${
      config.base_url
    }/${country.toLowerCase()}/${config.base_params}&app_id=${
      config.app_id
    }&app_key=${config.app_key}&where=${location}`;

    const results = await axios
      .get(targetURL)
      .then((resp) => {
        console.log(resp);
        // this.setState({jobs: resp.data.results})
        return resp.data.results;
      })
      .catch((error) => {
        console.log(error);
      });

    this.setState({ jobs: results });

    console.log("results:", results);
  };

  handleScroll = (e) => {
    const classList =
      e.target.scrollTop > 30 ? "header header-shadow" : "header";
    this.setState({ headerClass: classList });
  };

  handleJobClick = (bg, title) => {
    console.log(this.state.detailPage);
    this.setState({
      detailPage: !this.state.detailPage,
      jobBg: bg,
      jobDetailTitle: title,
    });
  };

  render() {
    return (
      <div className='job'>
        <Header headerClass={this.state.headerClass} />
        <Main
          handleJobClick={this.handleJobClick}
          detailPage={this.state.detailPage}
          jobBg={this.state.jobBg}
          jobs={this.state.jobs}
        />
      </div>
    );
  }
}

export default App;
