import React from "react";
import logo from "../assets/logo.svg";

function Header({ headerClass }) {
  const changeMode = () => {
    document.body.classList.toggle("dark-mode");
  };

  const handleLogoClick = () => {
    console.log("logo clicked..");
    // logo.addEventListener("click", () => {
    //   wrapper.classList.remove("detail-page");
    //   wrapper.scrollTop = 0;
    //   jobBg.style.background = bg;
    // });
    const wrapper = document.querySelector(".wrapper");
    wrapper.classList.remove("detail-page");

    // TO change later !! dont use querySelector
  };

  return (
    <div className={headerClass}>
      <div className='logo' onClick={handleLogoClick}>
        <img src={logo} alt='' style={{ width: "24px", marginRight: "12px" }} />
        Milao
      </div>

      <div className='header-menu'>
        <a href='#find_job' className='active'>
          Find Job
        </a>
        <a href='#company_review'>Company Review</a>
        <a href='#find_salaries'>Find Salaries</a>
      </div>

      <div className='user-settings'>
        <div className='dark-light' onClick={changeMode}>
          <svg
            viewBox='0 0 24 24'
            stroke='currentColor'
            strokeWidth='1.5'
            fill='none'
            strokeLinecap='round'
            strokeLinejoin='round'
          >
            <path d='M21 12.79A9 9 0 1111.21 3 7 7 0 0021 12.79z' />
          </svg>
        </div>

        <div className='user-menu'>
          <svg
            xmlns='http://www.w3.org/2000/svg'
            viewBox='0 0 24 24'
            fill='none'
            stroke='currentColor'
            strokeWidth='2'
            strokeLinecap='round'
            strokeLinejoin='round'
            className='feather feather-square'
          >
            <rect x='3' y='3' width='18' height='18' rx='2' ry='2' />
          </svg>
        </div>
        <img
          className='user-profile'
          src='https://s3-us-west-2.amazonaws.com/s.cdpn.io/3364143/download+%283%29+%281%29.png'
          alt=''
        />
        <div className='user-name'>Suhayel Nasim</div>
      </div>
    </div>
  );
}

export default Header;
